.signin-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(120deg, #d9f3ff 0%, #f7fbe8 100%);
}

.custom-signin-container {
  background: #fff;
  padding: 48px 40px 40px 40px;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.10);
  min-width: 420px;
  max-width: 440px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.signin-title {
  text-align: center;
  margin-bottom: 8px;
  font-size: 2rem;
  font-weight: 700;
}

.signin-desc {
  text-align: center;
  font-size: 14px;
  color: #888;
  margin-bottom: 32px;
  letter-spacing: 1px;
  font-weight: 500;
}

.custom-input {
  width: 100%;
  margin-bottom: 18px;
  padding: 13px 14px;
  border: 1.5px solid #bdbdbd;
  border-radius: 4px;
  font-size: 16px;
  box-sizing: border-box;
  background: #fff;
  transition: border 0.2s;
}
.custom-input:focus {
  border: 1.5px solid #2196f3;
  outline: none;
}

.custom-login-btn {
  width: 100%;
  height: 44px;
  background: #4b6ef5;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 17px;
  font-weight: 600;
  margin-bottom: 14px;
  margin-top: 4px;
  cursor: pointer;
  transition: background 0.2s;
}
.custom-login-btn:hover {
  background: #3451b2;
}

.custom-create-btn {
  width: 100%;
  height: 44px;
  background: #4b6ef5;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 32px;
  cursor: pointer;
  transition: background 0.2s;
}
.custom-create-btn:hover {
  background: #3451b2;
}

.custom-social {
  display: flex;
  justify-content: center;
  gap: 32px;
  margin-top: 0;
}

.social-btn {
  width: 44px;
  height: 44px;
  border: none;
  border-radius: 8px;
  background: #f5f5f5;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

.social-btn.google {
  background: url('https://cdn.jsdelivr.net/gh/devicons/devicon/icons/google/google-original.svg') center/60% no-repeat, #f5f5f5;
}

.social-btn.facebook {
  background: url('https://cdn.jsdelivr.net/gh/devicons/devicon/icons/facebook/facebook-original.svg') center/60% no-repeat, #f5f5f5;
}
