.signup-bg {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(120deg, #b2fefa 0%, #f6d365 100%);
}

.signup-container {
  background: #fff;
  padding: 40px 32px;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.10);
  min-width: 370px;
  max-width: 420px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.signup-container h2 {
  text-align: center;
  margin-bottom: 16px;
}

.signup-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.signup-input {
  padding: 10px 12px;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 15px;
}

.signup-id-row {
  display: flex;
  gap: 8px;
}

.signup-id-check {
  background: #eee;
  border: none;
  border-radius: 3px;
  padding: 0 12px;
  font-size: 13px;
  cursor: pointer;
}

.signup-birthday-row {
  display: flex;
  gap: 8px;
  width: 100%;
}

.signup-input.birth {
  flex: 1;
  min-width: 0;
  box-sizing: border-box;
}

.signup-check {
  font-size: 13px;
  margin: 8px 0 0 0;
  display: flex;
  align-items: center;
  gap: 6px;
}

.signup-btn {
  background: #222;
  color: #fff;
  border: none;
  border-radius: 2px;
  padding: 12px 0;
  font-weight: bold;
  font-size: 15px;
  margin-top: 10px;
  cursor: pointer;
  transition: background 0.2s;
}

.signup-btn:hover {
  background: #444;
}

.signup-or {
  text-align: center;
  color: #888;
  margin: 18px 0 8px 0;
  font-size: 13px;
}

.signup-social {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-bottom: 10px;
}

.social-btn {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 50%;
  background: #f5f5f5;
  cursor: pointer;
}

.social-btn.google {
  background: url('https://cdn.jsdelivr.net/gh/devicons/devicon/icons/google/google-original.svg') center/60% no-repeat, #f5f5f5;
}

.social-btn.facebook {
  background: url('https://cdn.jsdelivr.net/gh/devicons/devicon/icons/facebook/facebook-original.svg') center/60% no-repeat, #f5f5f5;
}

.signup-link {
  background: none;
  border: none;
  color: #1976ed;
  text-decoration: underline;
  cursor: pointer;
  font-size: 13px;
  margin-top: 8px;
} 